import React from "react";
import { EditorState, RichUtils } from "draft-js";
import Button from "../../../../../../../button";
import icon from "./align-center.svg";

interface AlignRightType {
  editorState: EditorState;
  setEditorState: React.Dispatch<React.SetStateAction<EditorState>>;
}

const AlignCenter: React.FC<AlignRightType> = ({
  editorState,
  setEditorState,
}) => {
  const [center, setCenter] = React.useState(false);

  React.useEffect(() => {
    const inlineStyle = editorState.getCurrentInlineStyle();
    setCenter(inlineStyle.has("center"));
  }, [editorState]);

  const centerHandler = () => {
    setEditorState(RichUtils.toggleInlineStyle(editorState, "center"));
  };

  return (
    <Button
      active={center}
      onMouseDown={(e) => e.preventDefault()}
      onClick={centerHandler}
      small
    >
      <img src={icon} alt="" />
    </Button>
  );
};

export default AlignCenter;
